  loadMetrics() {
    //------------------------CARREGA OS ITENS DE MÉTRICAS-----------------------------
    //1 - GUT, 2 - ESCALA QUALITATIVA, (3-17) - Métricas do questionário de teste
    this.priorizationProvider.getMetricItems(1)
      .then((metricItems: MetricItem[]) => {
        if (metricItems != null) {
          this.metricItems = metricItems;
          //------------------------CARREGA OS VALORES DE MÉTRICA-----------------------------
          this.priorizationProvider.getMetricValues()
            .then((result: MetricValue[]) => {
              if (result != null) {
                //------------------------ASSOCIA OS VALORES AOS ITENS-----------------------------
                for (let i = 0; i < this.metricItems.length; i++) {
                  let metricItem = this.metricItems[i];
                  for (let j = 0; j < result.length; j++) {
                    let metricValue = result[j];
                    if (metricItem.id == metricValue.metricItemId) {
                      this.metricItems[i].metricValues.push(metricValue);
                    }
                  }
                }
                //------------------------SALVA OS ITENS DE MÉTRICA-----------------------------
                this.storage.set('metricItems', this.metricItems);
              } else {
                this.showAlertLoadMetrics();
              }
            })
            .catch(() => {
              this.showAlertLoadMetrics();
            });
        } else {
          this.showAlertLoadMetrics();
        }
      })
      .catch(() => {
        this.showAlertLoadMetrics();
      });
  }

  showAlertLoadMetrics() {
    this.metricItems = [];
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.loadMetrics();
        }
      }]
    });
    alert.present();
  }

